body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgba(25,25,25);
  font-family: 'Oxygen', sans-serif;
}
.todo-heading {
  background: rgb(120, 0, 175);
  color: white;
  padding: 40px;
  text-align: center;
  font-size: 70px;
}
.p-float-label {
  margin: 0 auto;
  margin-left: 20%;
  position: relative;
  top: 15px;
}
.p-float-label #username {
  width: 75%;
  align-content: center;
  padding-top: 30px;
  padding-bottom: 30px;
  font-size: 30px;
}
#button1{
  position: relative;
  top: 50px;
  margin: 0 auto;
  text-align: center;
}
.list-group-item {
  color: white;
  background-color: rgb(108, 0, 141);
  position: relative;
  top: 60px;
  text-align: start;
  transition: 0.5s;
  border-radius: 60px;
  width: 90%;
  margin-left: 5%;
}
.list-group-item:hover {
  background-color: rgb(140, 0, 255);
  color: white;
}
.X {
  position: absolute;
  left: 97%;
  color: white;
  transition: 0.5s;
  cursor: default;
}
.X:hover {
  text-decoration: none;
  color: purple
}